<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info" v-if="this.products.length > 0">
        Колличество товаров : {{ this.products.length }}</span
      >
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem
              :item="item"
              v-for="item in products"
              :key="item.productId"
            ></CartItem>
            <div class="card__basket--empty" v-if="this.products.length === 0">
              <span class="card__basket--text">Корзина пуста</span>
              <router-link
                class="button card__basket--button button--primery"
                :to="{ name: 'main' }"
              >
                На главную
              </router-link>
            </div>
          </ul>
        </div>

        <div class="cart__block" v-if="this.products.length > 0">
          <p class="cart__desc">
            Мы посчитаем стоимость доставки на следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }}</span>
          </p>

          <router-link
            tag="button"
            :to="{ name: 'order' }"
            class="cart__button button button--primery"
            type="submit"
          >
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import { mapGetters } from "vuex";
import CartItem from "@/components/CartItem.vue";
export default {
  filters: { numberFormat },
  components: {
    CartItem,
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProduct",
      totalPrice: "cartTotalPrice",
    }),
  },
};
</script>
<style scoped>
.card__basket--empty {
  display: flex;
  flex-direction: column;
}
.card__basket--text {
  font-size: 50px;
}
.card__basket--button {
  text-align: left;
  border: 1px solid black;
  max-width: 150px;
  padding: 15px 8px;
  border-radius: 2px;
}
.card__basket--button:hover {
  background-color: #d4fa96;
  color: black;
}
.card__basket--button:active {
  background-color: #8bfc94;
}
</style>
