<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html"> Корзина </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span> №{{ this.$route.params.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за выбор нашего магазина. На Вашу почту придет письмо с
            деталями заказа. Наши менеджеры свяжутся с Вами в течение часа для
            уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key"> Получатель: </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Адрес доставки: </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Телефон: </span>
              <span class="dictionary__value"> {{ orderInfo.phone }}</span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email: </span>
              <span class="dictionary__value"> {{ orderInfo.email }} </span>
            </li>
            <!-- <li class="dictionary__item">
              <span class="dictionary__key"> Способ оплаты </span>
              <span class="dictionary__value"> картой при получении </span>
            </li> -->
            <li class="dictionary__item" v-if="orderInfo.comment">
              <span class="dictionary__key"> Коментарий к заказу </span>
              <span class="dictionary__value"> {{ orderInfo.comment }} </span>
            </li>
          </ul>
          <div class="card__basket--empty">
            <router-link
              class="button card__basket--button button--primery"
              :to="{ name: 'main' }"
            >
              Продолжить покупки
            </router-link>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in product" :key="item.id">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price | numberFormat }}</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>
              Итого: <b>{{ orderInfo.basket.items.length }}</b> товар на сумму
              <b>{{ orderInfo.totalPrice | numberFormat }}</b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>
<script>
import numberFormat from "@/helpers/numberFormat";
export default {
  filters: { numberFormat },
  data() {
    return {
      orderInfo: this.$store.state.orderInfo,
      product: this.$store.state.orderInfo.basket.items,
    };
  },
  created() {
    if (
      this.$store.state.orderInfo &&
      this.$store.state.orderInfo.id === this.$route.params.id
    ) {
      return;
    }
    this.$store.dispatch("loadOrderInfo", this.$route.params.id);
  },
};
</script>
<style scoped>
.card__basket--empty {
  width: 100%;
  margin-top: 70px;
  margin-left: auto;
  text-align: center;
}
.card__basket--empty :hover {
  color: black;
  border-color: #9eff00;
}
</style>
