<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="item.product.image"
        width="120"
        height="120"
        alt="Название товара"
      />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">Артикул: {{ item.product.id }} </span>
    <div class="product__color">
      <span class="color__title">Цвет:</span>
      <div class="color__value--container">
        <span
          class="colors__value"
          v-for="color in colors"
          :key="color.id"
          :style="{ backgroundColor: color.code }"
        ></span>
      </div>
    </div>
    <div class="product__counter form__counter">
      <button
        class="product__button product__button--minus"
        type="button"
        aria-label="Убрать один товар"
        @click.prevent="reduceQuantity(item.product.id)"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count" />

      <button
        class="product__button product__button--plus"
        type="button"
        aria-label="Добавить один товар"
        @click.prevent="increaseQuantity(item.product.id)"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }}</b
    >

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>
<script>
import numberFormat from "@/helpers/numberFormat";
import { mapMutations, mapActions } from "vuex";

export default {
  filters: { numberFormat },
  props: ["item"],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch("updateCartProductAmount", {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
    colors() {
      return this.item.product.colors;
    },
  },
  methods: {
    ...mapActions(["deleteCartProducts"]),
    ...mapMutations({
      increaseQuantity: "increaseQuantity",
      reduceQuantity: "reduceQuantity",
    }),
    deleteProduct() {
      this.deleteCartProducts({
        productId: this.item.productId,
      });
    },
  },
};
</script>
<style scoped>
.product__color {
  display: flex;
  flex-direction: column;
}
.product {
  grid-gap: 5px 14px;
}
.color__title {
  margin-bottom: 10px;
}
.color__value--container {
  max-width: 95px;
  display: flex;
  justify-content: space-between;
}
.product__button:hover {
  cursor: pointer;
}
.button-del:hover {
  cursor: pointer;
}
</style>
