<template>
  <li class="catalog__item" :product="product">
    <router-link
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id } }"
    >
      <img :src="product.image" :alt="product.title" />
    </router-link>
    <h3 class="catalog__title">
      <a href="#"> {{ product.title }}</a>
    </h3>
    <ul class="colors colors--black">
      <li v-for="color in colors" :key="color.id" class="colors__item">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" name="color" />
          <span class="colors__value" :style="{ backgroundColor: color.title }">
          </span>
        </label>
      </li>
    </ul>
    <span class="catalog__price"> {{ product.price | numberFormat }} </span>
  </li>
</template>

<script>
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";

export default {
  props: ["product"],
  filters: {
    numberFormat,
  },
  methods: {
    gotoPage,
  },
  computed: {
    colors() {
      return this.product.color;
    },
  },
};
</script>
