<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> 3 товара </span>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              :error="formError.name"
              title="ФИО"
              type="text"
              placeholder="Введите ваше полное имя"
            />
            <label class="form__label">
              <input
                v-model="formData.address"
                class="form__input"
                type="text"
                name="address"
                placeholder="Введите ваш адрес"
              />
              <span class="form__value">Адрес доставки</span>
              <span class="form__error" v-if="formError.address">{{
                formError.address
              }}</span>
            </label>

            <label class="form__label">
              <input
                v-model="formData.phone"
                class="form__input"
                type="tel"
                name="phone"
                placeholder="Введите ваш телефон"
              />
              <span class="form__value">Телефон</span>
              <span class="form__error" v-if="formError.phone">{{
                formError.phone
              }}</span>
            </label>

            <label class="form__label">
              <input
                class="form__input"
                v-model="formData.email"
                type="email"
                name="email"
                placeholder="Введи ваш Email"
              />
              <span class="form__value">Email</span>
              <span class="form__error" v-if="formError.email">{{
                formError.email
              }}</span>
            </label>
          </div>

          <BaseFormTextArea
            title="Коментарий к заказу"
            v-model="formData.comment"
            :error="formError.comment"
            placeholder="Ваши пожелания"
          />

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500"
                  />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card"
                  />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash"
                  />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.id">
              <h3>Название : {{ item.product.title }}</h3>
              <p>Количество : {{ item.amount }}</p>
              <b>
                Цена: {{ (item.amount * item.product.price) | numberFormat }}</b
              >
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>Бесплатно</b></p>
            <p>
              Итого: <b>{{ totalPrice | numberFormat }} </b>
            </p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            Похоже произошла ошибка. Попробуйте отправить снова или
            перезагрузите страницу.
          </p>
        </div>
      </form>
    </section>
  </main>
</template>
<script>
import BaseFormText from "@/components/BaseFormText.vue";
import BaseFormTextArea from "@/components/BaseFormTextArea.vue";
import { mapGetters } from "vuex";
import numberFormat from "@/helpers/numberFormat";
import { API_BASE_URL } from "@/config";
import axios from "axios";
export default {
  filters: { numberFormat },
  components: { BaseFormText, BaseFormTextArea },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: "",
      number: null,
    };
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = "";
      axios
        .post(
          API_BASE_URL + "/api/orders",
          {
            ...this.formData,
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          this.$store.commit("resetCart");
          this.$store.commit("updateOrderInfo", response.data);
          this.$router.push({
            name: "orderInfo",
            params: { id: response.data.id },
          });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        });
    },
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProduct",
      totalPrice: "cartTotalPrice",
    }),
  },
};
</script>
<style scoped>
.cart__order {
  display: flex;
  flex-direction: column;
  border: 1px solid white;
  padding: 10px;
  border-radius: 10px;
}
p {
  margin: 0;
}
</style>
